import { Pagination, Button, Select } from "antd";
import Loader from "../../../common/loader/Loader";
import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { getPayInReport } from "../../../api/login/Login";
import { FaSort } from "react-icons/fa";


function PayInReport() {
  const [data, setData] = useState([]);
  const [excelData, setExcelData] = useState([]);
  const [loading, setLoading] = useState(false);

  const [page, setPage] = useState(0);
  const [count, setCount] = useState(10);
  const [totalCount, setTotalCount] = useState(0);

  const [filter, setFilter] = useState({
    start_date: "",
    end_date: "",
    status: "",
    order_id: "",
    txn_id: "",
  });

  const currentDate = new Date().toISOString().split("T")[0];

  const fetchReport = async () => {
    try {
      setLoading(true);
      const res = await getPayInReport(page, count);

      setData(res?.data?.data);
      setExcelData(res?.data?.data);
      setTotalCount(res?.data?.pagination?.totalRecords);
    } catch (error) {
      console.log(error);
      toast.error("Failed to load PayIn Report");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchReport();
  }, [page, count]);

  const handlePageSizeChange = (value) => {
    setCount(value);
    setPage(0);
  };

  const onChangeVal = (value) => {
    setPage(value - 1);
  };

  const handleChange = (e) => {
    setFilter({ ...filter, [e.target.name]: e.target.value });
  };

  const ResetData = () => {
    setFilter({
      start_date: "",
      end_date: "",
      status: "",
      order_id: "",
      txn_id: "",
    });
    setPage(0);
    fetchReport();
  };

  const handleSearch = () => {
    setPage(0);
    fetchReport();
  };

  return (
    <>
      {loading && <Loader />}
      <div className="PageHeading">
        <h1
          style={{
            color: "var(--bs-primary2)",
            fontWeight: 600,
          }}
        >
          PayIn Transaction Report
        </h1>
      </div>

      <div
        className="ContentArea"
        style={{
          backgroundColor: "var(--bs-primary1)",
          color: "var(--bs-primary2)",
          border: "1px solid var(--bs-primary2)",
          borderRadius: "12px",
          padding: "16px",
        }}
      >
        <div
          className="card"
          style={{
            backgroundColor: "var(--bs-primary1)",
            border: "1px solid var(--bs-primary2)",
            borderRadius: "12px",
          }}
        >
          <div
            className="card-header"
            style={{
              backgroundColor: "var(--bs-primary3)",
              color: "var(--bs-primary2)",
              fontWeight: 600,
            }}
          ></div>
          <div className="card-body">
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-3">
                <label>Start Date</label>
                <input
                  type="date"
                  name="start_date"
                  className="form-control"
                  max={currentDate}
                  value={filter.start_date}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-3">
                <label>End Date</label>
                <input
                  type="date"
                  name="end_date"
                  className="form-control"
                  max={currentDate}
                  value={filter.end_date}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-3">
                <label>Status</label>
                <select
                  className="form-select"
                  name="status"
                  value={filter.status}
                  onChange={handleChange}
                >
                  <option selected>Select Status</option>
                  <option value=" ">All</option>
                  <option value="PENDING">Pending</option>
                  <option value="SUCCESS">Success</option>
                  <option value="FAILED">Failed</option>
                  <option value="REJECT">Rejected</option>
                </select>
              </div>

              <div className="form-group col-md-3">
                <label>Order Id</label>
                <input
                  type="text"
                  name="order_id"
                  className="form-control"
                  placeholder="Enter Order Id"
                  value={filter.order_id}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-3">
                <label>Txn Id</label>
                <input
                  type="text"
                  name="txn_id"
                  className="form-control"
                  placeholder="Enter Txn Id"
                  value={filter.txn_id}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-12">
  <label>&nbsp;</label>

  <button
    type="button"
    className="btn"
    onClick={handleSearch}
    style={{
      backgroundColor: "var(--bs-primary3)",
      color: "var(--bs-primary2)",
      border: "1px solid var(--bs-primary2)",
      fontWeight: 600,
      padding: "6px 16px",
      borderRadius: "10px",
      marginRight: "8px",
    }}
  >
    Search
  </button>

  <button
    type="button"
    className="btn"
    onClick={ResetData}
    style={{
      backgroundColor: "var(--bs-primary1)",
      color: "var(--bs-primary2)",
      border: "1px solid var(--bs-primary2)",
      fontWeight: 600,
      padding: "6px 16px",
      borderRadius: "10px",
    }}
  >
    Reset
  </button>
</div>

            </div>
          </div>
        </div>

        {/* Table */}
        <div
          className="card mt-4"
          style={{
            backgroundColor: "var(--bs-primary1)",
            border: "1px solid var(--bs-primary2)",
            borderRadius: "12px",
          }}
        >
          <div className="card-body">
            <div className="d-flex justify-content-end align-items-center mb-3">
              <Button
                onClick={() => document.getElementById("excel-btn").click()}
                style={{
                  backgroundColor: "#E8DAD6",
                  color: "var(--bs-primary2)",
                  border: "1px solid var(--bs-primary2)",
                }}
              >
                Download Excel
              </Button>
              <ReactHTMLTableToExcel
                id="excel-btn"
                className="d-none"
                table="table-to-xlsx"
                filename="PayIn_Report"
                sheet="Sheet1"
                buttonText="Download Excel"
              />
            </div>

            <div style={{ overflow: "auto" }}>
              <table
                className="table table-striped table-bordered table-hover"
                style={{
                  backgroundColor: "var(--bs-primary1)",
                  color: "var(--bs-primary2)",
                }}
              >
                <thead
  style={{
    backgroundColor: "var(--bs-primary3)",
    color: "var(--bs-primary2)",
    fontWeight: 600,
  }}
>
  <tr>
    <th className="text-center er-th">
      S.No <FaSort />
    </th>
    <th className="text-center er-th">
      Txn Date <FaSort />
    </th>
    <th className="text-center er-th">
      Order ID <FaSort />
    </th>
    <th className="text-center er-th">
      Txn ID <FaSort />
    </th>
    <th className="text-center er-th">
      Amount <FaSort />
    </th>
    <th className="text-center er-th">
      Charges <FaSort />
    </th>
    <th className="text-center er-th">
      Final Amount <FaSort />
    </th>
    <th className="text-center er-th">
      Status <FaSort />
    </th>
  </tr>
</thead>


                <tbody>
                  {data?.length > 0 &&
                    data.map((item, i) => (
                      <tr key={item._id}>
                        <td>{i + 1 + page * count}</td>
                        <td>{item?.createdAt}</td>
                        <td>{item?.merchantOrderId}</td>
                        <td>{item?.txnId}</td>
                        <td>{item?.amount}</td>
                        <td>{item?.charges}</td>
                        <td>{item?.final_amount}</td>
                        <td>
                          <span
                            className={`badge ${
                              item.status === "SUCCESS"
                                ? "bg-success"
                                : item.status === "PENDING"
                                ? "bg-warning"
                                : "bg-danger"
                            }`}
                          >
                            {item?.status}
                          </span>
                        </td>
                      </tr>
                    ))}

                  {data?.length === 0 && (
                    <tr>
                      <td colSpan="10" style={{ textAlign: "center" }}>
                        No Record Found
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>

          {/* Pagination & count */}
          <div className="dataTables_info_page d-flex justify-content-between align-items-center p-3">
            <div className="d-flex align-items-center">
              <label className="me-2">Show</label>
              <Select
                value={count}
                onChange={handlePageSizeChange}
                style={{ width: 120 }}
                options={[
                  { value: 10, label: "10 / page" },
                  { value: 20, label: "20 / page" },
                  { value: 50, label: "50 / page" },
                  { value: 100, label: "100 / page" },
                ]}
              />
              <div className="ms-3">Total {totalCount} entries</div>
            </div>

            <Pagination
              pageSize={count}
              current={page + 1}
              total={totalCount}
              onChange={onChangeVal}
              showSizeChanger={false}
              style={{ color: "var(--bs-primary2)" }}
            />
          </div>
        </div>
      </div>

      {/* Excel Table */}
      <table className="d-none" id="table-to-xlsx">
        <thead>
          <tr>
            <th>Created Date</th>
            <th>Txn Id</th>
            <th>Order Id</th>
            <th>Amount</th>
            <th>Charges</th>
            <th>Final Amount</th>
            <th>Status</th>
          </tr>
        </thead>
        <tbody>
          {excelData?.map((item) => (
            <tr key={item._id}>
              <td>{item?.createdAt}</td>
              <td>{item?.txnId}</td>
              <td>{item?.merchantOrderId}</td>
              <td>{item?.amount}</td>
              <td>{item?.charges}</td>
              <td>{item?.final_amount}</td>
              <td>{item?.status}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
}

export default PayInReport;
